.footer{
    color:#d9d9d9;
    background-color: #323232;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:20px;
    padding: 20px 8vw;
    padding-top: 80px;
    margin-top: 100px;
}
.footer-content{
    width:100%;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap:80px;
}
.name{
    font-style: outfit;
    color:tomato;
    font-weight: 5000;
}
.footer-content-left,.footer-content-right,.footer-content-center{
    display: flex;
    flex-direction: column;
    align-items: start;
    gap:20px;
}
.footer-content-left li,.footer-content-right li,.footer-content-center li{
    list-style:none;
    margin-bottom: 10px;
    cursor: pointer;
}
.footer-content-right h2,.footer-content-center h2{
    color:white;
}
.footer-social-icons img{
     width:40px;
     margin-right: 15px;
} 
.footer hr{
    width:1270px;
    height:2px;
    margin: 20px 0px;
    background-color: grey;
    border: none;
}
.ptag{
    max-width: 450px;
}
@media (max-width:750px){
    .footer-content{
        display: flex;
        flex-direction: column;
        gap: 35px;
    }
    .footer-copyright{
        text-align: center;
    }

}